  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: auto;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding-top: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320)));
  padding-bottom: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320)));
  padding-left: calc(15px + (30 - 15) * ((100vw - 320px) / (1920 - 320)));
  padding-right: calc(15px + (30 - 15) * ((100vw - 320px) / (1920 - 320)));
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.tab-product-main-tab .tab2-product ul li.current a {
  border-bottom: 2px solid #059fe2;
  color: #059fe2;
}

.tab-product-main-tab .tab2-product ul li.current::before {
  display: none;
}
.tab-product-main-tab .tab2-product ul li:nth-child(n + 2) {
  margin-left: calc(0px + (15 - 0) * ((100vw - 320px) / (1920 - 320)));
}

